const KopSurat = ({ judulSurat, children, ...rest }: KopSuratT) => {
    return (
        <td className="judul-surat form-control-input-surat" colSpan={3}>
            <h2>{judulSurat}</h2>
            <h2>
                NOMOR:
                <span {...rest}>{children}</span>
            </h2>
        </td>
    )
}
export default KopSurat
