import { Suspense, lazy, memo, useEffect, useState } from "react"
import { useNavigate, useParams, useSearchParams } from "react-router-dom"
import "../../Assets/Css/surat.css"
import KopSurat from "../Components/AjukanSurat/KopSurat"
import ButtonBack from "../Components/Buttons/ButtonBack"
import useDate from "../../Hooks/useDate"
import logo from "../../Assets/images/logo/logo-kota.svg"
import Button from "../Components/Buttons/Button"
import { get, put } from "../../Functions/Api"
import { elAll } from "../../Functions/GetElement"
import useForm from "../../Hooks/useForm"
import { PopupAlert } from "../../Functions/Alerts"

const EditDomisili = lazy(() => import("./EditDomisili"))
const EditSbm = lazy(() => import("./EditSbm"))
const EditSkck = lazy(() => import("./EditSkck"))
const EditSktm = lazy(() => import("./EditSktm"))
const EditSku = lazy(() => import("./EditSku"))
const EditSpp = lazy(() => import("./EditSpp"))

const EditSurat = () => {
    const { jenis_surat, id, type } = useParams()
    const jenisSurat = jenis_surat as SuratT
    const [searchParams] = useSearchParams()
    const judulSurat = searchParams.get("nama")?.toUpperCase() || ""
    const { Y, m, d } = useDate()
    const formBuilder = useForm()
    const navigate = useNavigate()

    const [dataSurat, setDataSurat] = useState<DataT>()
    const formControlInputSurat: string = "form-control-input-surat"

    useEffect(() => {
        const getDataSurat = async () => {
            const res = await get(`surat/${jenis_surat}/${id}`)
            if (res.ok) {
                setDataSurat(res.data)
            }
        }
        getDataSurat()
    }, [jenis_surat, id])

    const save: React.MouseEventHandler<HTMLButtonElement> = async () => {
        const inputElements = elAll(`.${formControlInputSurat}`)
        const childrenInput: UseFormChild[] = []
        inputElements.forEach((element, i) => {
            const { name } = (element as HTMLElement).dataset
            childrenInput.push({
                name: name || "",
                value: element.textContent || ""
            })
        })
        const form = formBuilder(childrenInput)

        const res = await put(`surat/${id}`, form)
        console.log(form)
        if (res.ok) {
            PopupAlert.fire({
                icon: "success",
                text: "Berhasil Menyimpan Data"
            })
        }
        setTimeout(() => navigate(`/${type}/data-surat`), 600)
    }

    return (
        <>
            <ButtonBack />
            <div className="surat shadow rounded-3">
                <section id="kop-surat">
                    <table>
                        <tbody>
                            <tr>
                                <td className="logo" colSpan={1}>
                                    <img src={logo} alt="logo-kelurahan" />
                                </td>
                                <td className="instansi" colSpan={2}>
                                    <h3>PEMERINTAHAN KOTA BENGKULU</h3>
                                    <h3>KECAMATAN SINGARAN PATI</h3>
                                    <h1>KELURAHAN JEMBATAN KECIL</h1>
                                    <p>
                                        Jl. Rinjani ayam RT X Nomor 1 Kelurahan
                                        Jembatan Kecil Kota Bengkulu Tlp. (0736)
                                        342645
                                    </p>
                                </td>
                            </tr>
                            <tr>
                                <KopSurat
                                    judulSurat={judulSurat}
                                    contentEditable
                                    suppressContentEditableWarning
                                    data-name="no_surat"
                                >
                                    200/OK/VI/2004
                                </KopSurat>
                            </tr>
                        </tbody>
                    </table>
                </section>
                {dataSurat && (
                    <Suspense>
                        {jenisSurat === "Domisili" && (
                            <EditDomisili
                                dataSurat={dataSurat}
                                formControlInputSurat={formControlInputSurat}
                            />
                        )}
                        {jenisSurat === "BelumMenikah" && (
                            <EditSbm
                                dataSurat={dataSurat}
                                formControlInputSurat={formControlInputSurat}
                            />
                        )}
                        {jenisSurat === "Skck" && (
                            <EditSkck
                                dataSurat={dataSurat as DataT}
                                formControlInputSurat={formControlInputSurat}
                            />
                        )}
                        {jenisSurat === "Sktm" && (
                            <EditSktm
                                dataSurat={dataSurat}
                                formControlInputSurat={formControlInputSurat}
                            />
                        )}
                        {jenisSurat === "KeteranganUsaha" && (
                            <EditSku
                                dataSurat={dataSurat}
                                formControlInputSurat={formControlInputSurat}
                            />
                        )}
                        {jenisSurat === "PengantarPernikahan" && <EditSpp />}
                    </Suspense>
                )}

                <section id="tanda-tangan">
                    <table>
                        <tbody>
                            <tr>
                                <td colSpan={3}>
                                    <div className="lokasi-tanggal">
                                        <p>DITETAPKAN DI : BENGKULU</p>
                                        <p>PADA TANGGAL : {`${d} ${m} ${Y}`}</p>
                                    </div>
                                    <div className="lurah">
                                        <p>KA. KELURAHAN JEMBATAN KECIL</p>
                                    </div>
                                    <div className="ttd-lurah">
                                        <p>Lilis Suryani, SP.</p>
                                        <p>NIP. 198002092005022006</p>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </section>
            </div>
            <div className="action my-5 d-flex justify-content-end gap-3 px-5 mx-5">
                <Button className="btn-primary" text="Simpan" onClick={save} />
                <Button
                    className="btn-info"
                    text="Cetak"
                    onClick={() => window.print()}
                />
            </div>
        </>
    )
}

export default memo(EditSurat)
