const useDate = () => {
    const months = [
        "Januari",
        "Februari",
        "Maret",
        "April",
        "Mei",
        "Juni",
        "Juli",
        "Agustus",
        "September",
        "Oktober",
        "November",
        "Desember"
    ]
    const date = new Date()
    const letterDate = {
        Y: date.getFullYear(),
        m: months[date.getMonth()],
        d: date.getDate(),
        date
    }

    return letterDate
}

export default useDate
